











































import Vue from 'vue';

export default Vue.extend( {
    name: "DefaultAppBar",
    props: {
      name: String,
    },
  mounted() {
      if (this.isOnline === false) {
        this.$store.dispatch('initWebSocket')
      }
  },
  methods: {
        reset() {
            localStorage.setItem('sessionKey', '');
            localStorage.setItem('userSecret', '');
        }
    },
  computed: {
      isOnline(): boolean {
        return this.$store.getters.isConnected()
      }
  }
})
